import { useState } from "react";
import { ModalAuth } from "../ModalAuth";
import { useDispatch, useSelector } from "react-redux";
import { logOutAction } from "src/store/authSlice";
import { Button } from "antd";
import styles from "./styles.module.scss";
import { URL } from "src/routes/constants";

export default function Header() {
  const [visible, setVisible] = useState<boolean>(false);

  const { loading, auth, user } = useSelector(
    (state: any) => state?.authReducer
  );
  const dispatch = useDispatch();

  const handleAuth = () => {
    if (auth) {
      dispatch(logOutAction());
    } else {
      setVisible(true);
    }
  };

  return (
    <nav className="navbar navbar-expand-lg bg-light">
      <div className="container-fluid">
        <a className="navbar-brand" href="/test-nal">
          BLOG-INTERVIEW TEXT
        </a>
        <div className={styles.avatarWrap}>
          {auth && (
            <img
              alt="avatar"
              src={
                user?.avatar?.url
                  ? user?.avatar?.url
                  : "data:image/jpeg;base64,"
              }
            />
          )}
          <Button
            onClick={handleAuth}
            className="btn btn-primary"
            loading={loading}
          >
            {auth ? "LOG OUT" : "LOG IN"}
          </Button>
        </div>
      </div>
      {visible && (
        <ModalAuth visible={visible} onCancel={() => setVisible(false)} />
      )}
    </nav>
  );
}
